import empty from '@/components/empty.vue';
import axios from '@/utils/uniAxios.js'
export default {
	components: {
		empty
	},
	data() {
		return {
			src: '../static/shopcarempty.png',
			msg: '购物车没有商品，你还可以',
			list: [],
			update: '编辑',
			checked: true,
			stepValue: 1,
			num: 1,
			deleShow: false,
			list: [],
			checkeds: false,
			total: 0,
			allNum: 0, //总的数量
			deleIds: [],
			checkNum:0
		}
	},
	onLoad(option) {
		this.getShopCar();
	},
	onShow() {
		this.getShopCar();
	},
	methods: {
		btn(){
			uni.reLaunch({
				url:'../index/index'
			})
		},
		// 结算
		payGoods(){
			if(this.allNum==0){
				this.$api.msg('请选择商品')
			}else{
				uni.reLaunch({	
					url:'postOrder?deleIds='+JSON.stringify(this.deleIds)
				})
			}
			
		},
		// 促销信息
		promote(n) {
			var str = n.split('：');
			return str;
		},
		// 获取某个商品的规格
		changeAttr(id) {
			axios.post('/cart/getProduct/' + id).then(res => {
				// 弹出规格的弹窗
			})
		},
		// 修改优惠
		editPrefe() {

		},
		// 获取购物车列表
		getShopCar() {
			axios.post('/cart/list/promotion').then(res => {
				if (res.data.code == 200) {
					this.list = res.data.data
				}
			})
		},
		// 点击编辑或完成
		edit() {
			if (this.update == '编辑') {
				this.update = '完成';
				this.deleShow = true;
				this.total = 0;
				this.allNum = 0;
				this.deleIds = [];
				// 处理完成状态时，请求接口对商品进行处理
				this.list.forEach(el => {
					el.deleteStatus = 0; //全都不选中
				})
				this.checkeds = false;
			} else {
				this.update = '编辑';
				this.deleShow = false;
				this.checkeds = false;
				this.total = 0;
				this.list.forEach(el => {
					el.deleteStatus = 0; //全都不选中
				})
				// 处于编辑状态时，不需要做处理
			}
		},
		// 最终的全选
		allSlect(e) {
			// 全选，加入所有商品的ID
			this.checkeds = e.detail;
			if (this.checkeds) { //全部选择
				this.total = 0;
				let totalPrices = '';
				this.list.forEach((el, index) => {
					el.deleteStatus = 1;
					totalPrices = Number(((el.price*100).toFixed(2))* el.quantity);
					this.total += totalPrices;
				})
				this.total = (this.total/100).toFixed(2)
				this.allNum = this.list.length;
				this.list.forEach((el, index) => {
					this.deleIds.push(el.id)
				})
				this.checkNum = this.list.length;
			} else { //全不选中
				this.total = 0;
				this.list.forEach((el, index) => {
					el.deleteStatus = 0;
				});
				this.allNum = 0;
				this.deleIds = [];
				this.checkNum = 0
			}

		},
		//每个商店的全选
		onChangeAll() {

		},
		// 删除某个商品
		deleteGoods() {
			let obj = {
				ids:this.deleIds
			}
			if (this.deleIds.length == 0) {
				this.$api.msg('请选择商品')
			} else {
				axios.post('/cart/delete',obj).then(res => {
					if (res.data.code == 200) {
						this.$api.msg('删除成功');
						this.getShopCar();
					}
				})
			}
		},
		// 单个商品的选择
		singleOnChange(status, index, id) {			
			// 首选判断是否处于删除状态
			if (this.deleShow) { //表示删除状态
				if (status == 1) {
					// 取消选中
					this.list[index].deleteStatus = 0;
					this.list.forEach((el, index) => {
						if (el.id == id) {
							this.deleIds.splice(index, 1)
						}
					})
					this.checkNum--;
					this.checkeds = false
				} else { //选中
					this.list[index].deleteStatus = 1;
					this.deleIds.push(id);
					this.checkNum++;
					if(this.checkNum==this.list.length){
						this.checkeds = true;
					}
				}
			} else { //表示订单状态时
				if (status == 1) { //表示取消选中
					this.list[index].deleteStatus = 0;
					let price = Number(((this.list[index].price*100).toFixed(2)) * this.list[index].quantity);
					let totalShortTime = Number((this.total*100).toFixed(2))
					totalShortTime -= price
					this.total = (totalShortTime/100).toFixed(2)
					this.allNum--;
					this.deleIds.splice(this.deleIds.findIndex(item=>item==index),1);
					this.checkNum--;
					this.checkeds = false
				} else {
					this.checkNum++;
					this.list[index].deleteStatus = 1;
					let price = Number(((this.list[index].price*100).toFixed(2)) * this.list[index].quantity);
					let totalShortTime = Number((this.total*100).toFixed(2));
					totalShortTime += price;
					this.total = (totalShortTime/100).toFixed(2);
					this.$store.commit('totalMoney', price);
					this.allNum++;
					this.deleIds.push(id)
					console.log(this.checkNum)
					if(this.checkNum==this.list.length){
						this.checkeds = true
					}
				}
			}
		},
		// 减少商品数量
		// 同时判断总价
		minus(item, index, id, realStock) {
			if (item.quantity == 1) {
				item.quantity = 1;
			} else {
				item.quantity--;
				this.list[index].realStock++;
				realStock++;
				if (item.deleteStatus == 1) { //表示选中
					let totalShortTime = (this.total*100).toFixed(2);
					let oriceShortTime = (item.price*100).toFixed(2);
					totalShortTime -= oriceShortTime;
					this.total = (totalShortTime/100).toFixed(2)
				}
			}
			let obj = {
				id: id,
				quantity: item.quantity
			}
			axios.post('/cart/update/quantity', obj).then(res => {

			})
		},
		// 增加商品数量
		add(item, index, id, realStock) {
			item.quantity++;
			realStock--;
			this.list[index].realStock--;
			if (item.deleteStatus == 1) { //表示选中
				let addTotalShortTime = Number((this.total*100).toFixed(2));
				let addPiceShortTime = Number((item.price*100*1).toFixed(2));
				addTotalShortTime += addPiceShortTime;
				this.total = (addTotalShortTime/100).toFixed(2)
			}
			// 请求接口
			let obj = {
				id: id,
				quantity: item.quantity
			}
			axios.post('/cart/update/quantity', obj).then(res => {

			})
		}
	}
}
